

$('#clock').countdown('2022/12/25 24:00:00')
  .on('update.countdown', function (event) {
    var startHour = '<div class="timer-iner"> <span class="time">%H</span> <span>Hours</span> </div>';
    var startMinutes = '<div class="timer-iner"> <span class="time">%M</span> <span>Minutes</span> </div>';
    var startSeconds = '<div class="timer-iner"> <span class="time">%S</span> <span>Seconds</span> </div>';
    var format = `${startHour + startMinutes + startSeconds}`;
    if (event.offset.totalDays > 0) {
      format = '<div class="timer-iner"> <span class="time">%-d</span> <span>day%!d</span> </div>' + format;
    }
    if (event.offset.weeks > 0) {
      format = '<div class="timer-iner"><span class="time"> %-w </span> <span>week%!w</span> </div>' + format;
    }
    $(this).html(event.strftime(format));
  })
  .on('finish.countdown', function (event) {
    $(this).html('<div class="expired body-lg-b">This offer has been expired</div>')
      .parent().addClass('disabled');

  });

$(window).on('load', function () {
  $('.loader').fadeOut();
});

AOS.init({
  once: true,
});

